import { motion } from "framer-motion";
import {
    FaArrowLeft,
    FaClock, FaTrophy,
    FaUsers
} from "react-icons/fa";
import { Link } from "react-router-dom";
import certifImg from "../../assets/images/certif.png";
import flogo from "../../assets/images/flogo.png";
import Footer from "../../components/Footer";
import Navbar from "../../components/Navbar";
import ParticlesBackground from "../../components/ParticlesBackground";
import ScrollToTop from "../../components/ScrollToTop";

const NewsIEEEXtreme = () => {
  // Animation Variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { staggerChildren: 0.15 },
    },
  };

  const fadeInUp = {
    hidden: { opacity: 0, y: 30 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.6 } },
  };

  return (
    <div className="relative min-h-screen font-sans text-[#e3f2fd] overflow-x-hidden">
      <ScrollToTop />
      {/* Background Gradient */}
      <div className="absolute inset-0 bg-gradient-to-br from-[#1a237e] to-[#283593] -z-20"></div>

      {/* Particles */}
      <div className="absolute inset-0 -z-10 opacity-30">
        <ParticlesBackground />
      </div>

      <Navbar />

      <div className="pt-32 pb-20 px-4 sm:px-6 lg:px-8 max-w-7xl mx-auto relative z-10">
        {/* Header / Breadcrumb */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-12 p-6 rounded-[24px] border border-[rgba(79,195,247,0.2)] bg-[rgba(26,35,126,0.6)] backdrop-blur-[16px] shadow-[0_8px_32px_0_rgba(0,0,0,0.37)] flex justify-between items-center"
        >
          <div className="flex items-center gap-4">
            <Link
              to="/news"
              className="text-[#81d4fa] hover:text-white transition-colors flex items-center gap-2"
            >
              <FaArrowLeft /> Back to News
            </Link>
          </div>
          <img src={flogo} alt="Logo" className="h-8 w-auto opacity-80" />
        </motion.div>

        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          className="grid grid-cols-1 lg:grid-cols-12 gap-8"
        >
          {/* Left Column (Main Content) */}
          <div className="lg:col-span-8 space-y-8">
            {/* Hero Block */}
            <motion.div
              variants={fadeInUp}
              className="p-10 rounded-[24px] border border-[rgba(79,195,247,0.2)] bg-[rgba(26,35,126,0.6)] backdrop-blur-[16px] shadow-lg relative overflow-hidden"
            >
              <div className="absolute top-0 right-0 w-64 h-64 bg-[#05608a] rounded-full filter blur-[100px] opacity-20 -translate-y-1/2 translate-x-1/2"></div>

              <div className="relative z-10">
                <span className="inline-block py-1 px-3 rounded-full bg-[#05608a]/30 border border-[#81d4fa]/30 text-[#81d4fa] text-xs font-bold uppercase tracking-widest mb-4">
                  Competition
                </span>
                <h1 className="text-4xl md:text-5xl font-extrabold text-white mb-6 bg-clip-text text-transparent bg-gradient-to-r from-white to-[#81d4fa]">
                  IEEEXtreme 18.0 Journey
                </h1>
                <p className="text-lg md:text-xl leading-relaxed text-[#e3f2fd]/90">
                  IEEEXtreme is one of the world's largest competitive
                  programming competitions, testing participants' coding skills,
                  teamwork, and problem-solving abilities in an intense 24-hour
                  marathon.
                </p>
              </div>
            </motion.div>

            {/* Content Body */}
            <motion.div variants={fadeInUp}>
              <div className="p-8 rounded-[24px] border border-[rgba(79,195,247,0.2)] bg-[rgba(26,35,126,0.4)] backdrop-blur-[12px] mb-8">
                <h2 className="text-2xl font-bold text-white mb-6 pl-4 border-l-4 border-[#05608a]">
                  The Challenge
                </h2>
                <p className="text-[#e3f2fd]/80 leading-relaxed mb-6">
                  Participating in IEEEXtreme 18.0 was an exhilarating
                  experience. For 24 hours straight, teams from across the globe
                  pushed their limits to solve complex algorithmic problems. It
                  wasn't just about coding; it was about endurance, strategy,
                  and collaboration under pressure.
                </p>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-8">
                  <div className="p-6 rounded-2xl bg-[#1a237e]/40 border border-[#81d4fa]/10 hover:border-[#81d4fa]/30 transition-all">
                    <div className="w-12 h-12 rounded-full bg-[#05608a]/20 flex items-center justify-center mb-4 text-[#81d4fa]">
                      <FaClock className="text-2xl" />
                    </div>
                    <h3 className="text-lg font-semibold text-white mb-2">
                      24-Hour Marathon
                    </h3>
                    <p className="text-sm text-[#e3f2fd]/70">
                      Non-stop problem solving against the clock.
                    </p>
                  </div>
                  <div className="p-6 rounded-2xl bg-[#1a237e]/40 border border-[#81d4fa]/10 hover:border-[#81d4fa]/30 transition-all">
                    <div className="w-12 h-12 rounded-full bg-[#05608a]/20 flex items-center justify-center mb-4 text-[#81d4fa]">
                      <FaUsers className="text-2xl" />
                    </div>
                    <h3 className="text-lg font-semibold text-white mb-2">
                      Team Synergy
                    </h3>
                    <p className="text-sm text-[#e3f2fd]/70">
                      Collaborating effectively to tackle hard problems.
                    </p>
                  </div>
                </div>
              </div>

              {/* Image */}
              <div className="rounded-[24px] overflow-hidden border border-[rgba(79,195,247,0.2)] shadow-lg">
                <img
                  src={certifImg}
                  alt="IEEEXtreme Certificate"
                  className="w-full h-auto object-cover hover:scale-105 transition-transform duration-700"
                />
                <div className="p-4 bg-[rgba(26,35,126,0.8)] backdrop-blur-md">
                  <p className="text-center text-sm text-[#81d4fa]">
                    Official Certificate of Participation
                  </p>
                </div>
              </div>
            </motion.div>
          </div>

          {/* Right Column (Sticky Sidebar) */}
          <div className="lg:col-span-4 space-y-8">
            <motion.div
              variants={fadeInUp}
              className="sticky top-32 p-8 rounded-[24px] border border-[rgba(79,195,247,0.2)] bg-[rgba(26,35,126,0.8)] backdrop-blur-[20px] shadow-xl"
            >
              <h3 className="text-xl font-bold text-white mb-6 border-l-4 border-[#05608a] pl-4 flex items-center gap-3">
                <FaTrophy className="text-[#ffd700]" /> Event Details
              </h3>
              <ul className="space-y-6">
                <li className="flex flex-col border-b border-white/5 pb-4 last:border-0 last:pb-0">
                  <span className="text-[#81d4fa] text-xs uppercase tracking-wider mb-1">
                    Event
                  </span>
                  <span className="text-white font-medium">
                    IEEEXtreme 18.0
                  </span>
                </li>
                <li className="flex flex-col border-b border-white/5 pb-4 last:border-0 last:pb-0">
                  <span className="text-[#81d4fa] text-xs uppercase tracking-wider mb-1">
                    Date
                  </span>
                  <span className="text-white font-medium">Mars 15, 2025</span>
                </li>
                <li className="flex flex-col border-b border-white/5 pb-4 last:border-0 last:pb-0">
                  <span className="text-[#81d4fa] text-xs uppercase tracking-wider mb-1">
                    Category
                  </span>
                  <span className="text-white font-medium">
                    Competitive Programming
                  </span>
                </li>
                <li className="flex flex-col border-b border-white/5 pb-4 last:border-0 last:pb-0">
                  <span className="text-[#81d4fa] text-xs uppercase tracking-wider mb-1">
                    Skills Used
                  </span>
                  <div className="flex flex-wrap gap-2 mt-2">
                    <span className="px-2 py-1 bg-[#05608a]/30 rounded text-xs text-[#e3f2fd] border border-[#05608a]/50">
                      Algorithms
                    </span>
                    <span className="px-2 py-1 bg-[#05608a]/30 rounded text-xs text-[#e3f2fd] border border-[#05608a]/50">
                      C++
                    </span>
                    <span className="px-2 py-1 bg-[#05608a]/30 rounded text-xs text-[#e3f2fd] border border-[#05608a]/50">
                      Data Structures
                    </span>
                  </div>
                </li>
              </ul>
            </motion.div>
          </div>
        </motion.div>
      </div>

      <Footer />
    </div>
  );
};

export default NewsIEEEXtreme;
